import * as React from "react";
import Box from "@mui/material/Box";
import { Divider, Drawer, IconButton, List, Stack } from "@mui/material";
import RegisterIcon from "@mui/icons-material/LockPersonOutlined";
import LoginIcon from "@mui/icons-material/VpnKey";
import CommentIcon from "@mui/icons-material/ChatBubble";
import PageViewIcon from "@mui/icons-material/FindInPage";
import PageAddIcon from "@mui/icons-material/PostAdd";
import LogOutIcon from "@mui/icons-material/Lock";
import SideBarIconButton from "./sidebar_icon_button";
import MenuIcon from "@mui/icons-material/Menu";

export default function SideBar() {
  const [drawerOpen, setDrawerOpen] = React.useState(false);

  return (
    <Box>
      <Stack direction="row" spacing={2} display={{ xs: "block", md: "none" }}>
        <IconButton onClick={() => setDrawerOpen(true)}>
          <MenuIcon></MenuIcon>
        </IconButton>
      </Stack>

      <Drawer
        anchor="left"
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
      >
        <List>
          {[
            {
              text: " ",
              icon: <PageViewIcon />,
              link: "",
              disabled: false,
            },
            {
              text: " ",
              icon: <PageAddIcon />,
              link: "/thread/write",
              disabled: false,
            },
          ].map((item_data, idx) => (
            <SideBarIconButton
              key={idx}
              text={item_data.text}
              icon={item_data.icon}
              link={item_data.link}
              disabled={item_data.disabled}
            />
          ))}
        </List>

        <Divider />

        <List>
          {[
            {
              text: " ",
              icon: <CommentIcon />,
              link: "",
              disabled: false,
            },
          ].map((item_data, idx) => (
            <SideBarIconButton
              key={idx}
              text={item_data.text}
              icon={item_data.icon}
              link={item_data.link}
              disabled={item_data.disabled}
            />
          ))}
        </List>

        <Divider />

        <List>
          {[
            {
              text: "",
              icon: <LoginIcon />,
              link: "/login",
              disabled: localStorage.getItem("user_nickname") !== null,
            },
            {
              text: "",
              icon: <RegisterIcon />,
              link: "/register",
              disabled: localStorage.getItem("user_nickname") !== null,
            },
            {
              text: "",
              icon: <LogOutIcon />,
              link: "/logout",
              disabled: localStorage.getItem("user_nickname") === null,
            },
          ].map((item_data, idx) => (
            <SideBarIconButton
              key={idx}
              text={item_data.text}
              icon={item_data.icon}
              link={item_data.link}
              disabled={item_data.disabled}
            />
          ))}
        </List>
      </Drawer>
    </Box>
  );
}
